import React from 'react'
import styled from 'styled-components';
function Login() {
    return (
        <Container>
            <CTA>
                <CTALogoOne src="/images/cta-logo-one.svg"/>
                <SignUp>GET ALL THERE</SignUp>
                <Description>
                    A login is a set of credentials used to authenticate a 
                    user. Most often, these consist of a username and 
                    password. However, a login may include other information,
                     such as a PIN number, passcode, or passphrase.Logins are used by websites, computer applications, and mobile apps
                </Description>
                <CTALogoTwo src="/images/cta-logo-two.png"/>
            </CTA>
        </Container>
    )
}

export default Login
const Container = styled.div`
    min-height:calc(100vh - 70px);
    
    position:relative;
    display:flex;
    align-items:top;
    justify-content:center;
    &:before{
        position:absolute;
        content:"";
        top:0;
        bottom:0;
        left:0;
        right:0;
        background-image:url("/images/login-background.jpg");
        background-position:top;
        background-size:cover;
        background-repeat:no-repeat;
        z-index:-1;
        opacity:0.7;

    }

`

const CTA = styled.div`
    max-width:650px;
    margin-top:50px;
    padding:80px 40px;
    width:90%;
    display:flex;
    
    flex-direction:column;


`
const CTALogoOne = styled.img`

`
const CTALogoTwo = styled.img`

`
const SignUp = styled.a`
    width:100%;
    background-color:#0063e5;
    font-weight:bold;
    padding:17px 0;
    color:#f9f9f9;
    border-radius:4px;
    text-align:center;
    font-size:18px;
    cursor:pointer;
    transition:all 250px;
    margin-top:8px;
    margin-bottom:12px;
    &:hover{
        background-color:#0483ee;
    }

`
const Description = styled.p`
    font-size:11px;
    letter-spacing:1.5px;
    text-align:center;
    line-height:1.5;

`